.btn {
    margin-bottom: 24px;
    cursor: pointer;
    align-items: center;
    justify-content: center;
    border-radius: 16px;
    border: 2px solid #3E85F3;
    background-color: #3E85F3;
}
.btn_text {
    font-family: Inter;
    font-weight: 600;
    line-height: 0.8;
    color: #FFF;
}
@media (max-width: 375px) {
    .btn {
        margin-top: 424px;
        margin-left: 20px;
        display: flex;
        gap: 6px;
    }
    .btn_text {
        padding: 14px 0 14px 28.5px;
        font-size: 14px;
        letter-spacing: -0.28px;
    }
    .icon {
        width: 18px;
        height: 18px;
        padding-right: 28.5px;
    }
}
@media (min-width: 375px) {
    .btn {
        margin-top: 424px;
        margin-left: 20px;
        display: flex;
        gap: 6px;
    }
    .btn_text {
        padding: 14px 0 14px 28.5px;
        font-size: 14px;
        letter-spacing: -0.28px;
    }
    .icon {
        width: 18px;
        height: 18px;
        padding-right: 28.5px;
    }
}
@media (min-width: 768px) {
    .btn {
        margin-top: 563px;
        margin-left: 32px;
        display: inline-flex;
        gap: 11px;
    }
    .btn_text {
        padding: 16px 0 16px 23px;
        font-size: 18px;
        letter-spacing: -0.36px;
    }
    .icon {
        width: 20px;
        height: 20px;
        padding-right: 23px;
    }
}
@media (min-width: 1440px) {
    .btn {
        margin-top: 439px;
        margin-left: 24px;
    }
}